"use client";

import { Sidebar, SidebarBody, SidebarLink, useSidebar } from "@/components/ui/sidebar";
import { useNavLinks } from "@/lib/nav-links";
import { usePathname } from "next/navigation";
import { cn } from "@/lib/utils";
import { motion } from "motion/react";

export function SidebarNavWithContent({ children }: { children: React.ReactNode }) {
  const navLinks = useNavLinks();
  const pathname = usePathname();

  return (
    <Sidebar>
      <SidebarBody className="flex flex-col">
        <div className="mb-8 mt-4">
          <h2 className="text-lg font-semibold text-text-primary">K2 Lab</h2>
        </div>
        <nav className="flex flex-col gap-2">
          {navLinks.map((link) => {
            const isActive = pathname === link.href;
            return (
              <SidebarLink
                key={link.href}
                link={{
                  label: link.label,
                  href: link.href,
                  icon: <link.icon className="h-5 w-5 text-text-secondary group-hover/sidebar:text-neon-green transition-colors" />
                }}
                className={cn(
                  isActive && "bg-surface-hover/70 text-neon-green border-l-2 border-neon-green"
                )}
              />
            );
          })}
        </nav>
      </SidebarBody>
      <ContentWrapper>{children}</ContentWrapper>
    </Sidebar>
  );
}

function ContentWrapper({ children }: { children: React.ReactNode }) {
  const { open, animate } = useSidebar();
  const sidebarWidth = animate ? (open ? 300 : 80) : 300;

  return (
    <motion.div
      animate={{
        marginLeft: `${sidebarWidth}px`,
      }}
      transition={{
        duration: 0.3,
        ease: [0.22, 1, 0.36, 1],
      }}
      className="min-h-screen"
    >
      {children}
    </motion.div>
  );
}




